.main {
  margin-top: 5em;
}

.ui.list {
  margin-top: 1em;
}

.main.container {
  padding-top: 100px;
}


.center {
  justify-content: center;
  padding: 10px;
}

.right {
float:right;
}

.ui.search input {
  width: 100%;
  border-radius: 0 !important;
}

.item {
  padding: 15px 0px !important;
  display:flex;
}

i.icon {
  float: right;
  font-size: 20px;
  cursor: pointer;
}

.center-div {
  text-align: center;

}

.contact{
display:inline-block;
padding-left:20px;  
}

.ui.icon.input{
  width:100%;
}

.modalContainer{
  
  top:0;
  left:0;
  z-index:999;
  width: 100vw;
  height: 100vh;
  background-color: rgba(200, 200, 200, 0.8);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;


}

.modal{
  width: 90vw;
  border-radius: 12px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  flex-direction: column;
  padding: 25px;

}

.buttonContainer{
  display:flex;
}

.modalYesButton{

  display:inline-block;
  max-width:150px;
  padding-right:20px;

}

.modalCancelButton{
  display:inline-block;
  max-width:150px;
}